<template>
  <div id="app">
    <span>{{ hash }}</span>
    <input type="text" v-model="nonce">
    <button v-on:click="calculateHash">
  </div>
</template>

<script>
export default {
  name: 'App',
  data: function () {
    return {
      hash: ''
    }
  },
  methods: {
    calculateHash: function () {
      this.hash = this.$store.state.block.getters.calculateHash
    }
  },
  computed: {
    nonce: {
      get () { return this.$store.state.block.getters.nonce },
      set (value) { this.$store.state.block.dispatch('changeNonce', value) }
    }
  }
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
</style>



