import 'babel-polyfill'
import Vuex from 'vuex'
import Vue from 'vue'
import sha256 from 'crypto-js/sha256'

Vue.use(Vuex)

const block = {
  state: {
    previousHash: null,
    timestamp: null,
    nonce: null,
    data: null
  },
  getters: {
    nonce (state) {
      return state.nonce
    },
    calculateHash (state) {
      return sha256(state.previousHash + state.timestamp + state.nonce + state.data).toString()
    }
  },
  mutations: {
    changeNonce (state, newNonce) {
      state.nonce = newNonce
    }
  },
  actions: {
    changeNonce ({ commit }, newNonce) {
      commit('changeNonce', newNonce)
    }
  }
}

const blockchain = {
  state: {
    blocks: []
  },
  mutations: {
    verify (newBlock) {
    },
    push (state, newBlock) {
      state.blocks.push(newBlock)
    }
  }
}

const store = new Vuex.Store({
  modules: {
    block,
    blockchain
  }
})

export default store



